import React, {Component} from 'react'
import { Card, Table, Button, Icon, message, Modal } from 'antd'
import LinkButton from '../../components/link-button' 
import {reqCategorys, reqAddCategory, reqUpdateCategory} from "../../api";
import AddForm from './add-form'
import UpdateForm from './update-form' 


export default class Category extends Component{

    state = { 
        categorys: [], 
        subCategorys: [], 
        parentId: '0',
        parentName: '', 
        loading: false,
        showStatus: 0,
        }

    getCategorys = async (parentId)=>{
        this.setState({
            loading:true
        })

        parentId = parentId || this.state.parentId
        const result = await reqCategorys(parentId)
        
        this.setState({
            loading:false
        })

        if(result.status===0){
            const categorys = result.data 
            if(parentId==='0'){
                this.setState({
                    categorys
                })
            }else{
                this.setState({
                    subCategorys:categorys
                })
            }
        }else{
            message.error('获取列表失败')
        }
    }

    showCategorys=()=>{
        this.setState ({ 
            subCategorys: [], 
            parentId: '0',
            parentName: '', 
            showStatus: 0,
            })
        }

        showSubCates=(category)=>{
            this.setState({
                parentId: category._id,
                parentName: category.name
            },()=>{
                this.getCategorys()
            })
        }

        showAdd = () => { 
            this.setState({
                 showStatus: 1 
                }) 
            }
            
            showUpdate = (category) => {
                   this.category = category  
                   this.setState({ 
                       showStatus: 2 
                    }) 
                }

            addCategory = async()=>{
                const {parentId, categoryName} = this.form.getFieldsValue()
                this.setState({ showStatus: 0 })
                this.form.resetFields()
                const result = await reqAddCategory(parentId,categoryName)
                if(result.status===0) {
                    if(parentId===this.state.parentId){
                        this.getCategorys()
                    }else if(parentId==='0'){
                        this.getCategorys(parentId)
                    }
                }
            }
            updateCategory = async() =>{
                const categoryId = this.category._id
                const{categoryName} = this.form.getFieldsValue()
                
                this.setState({ 
                    showStatus: 0 
                })
                this.form.resetFields()
                const result = await reqUpdateCategory(categoryId, categoryName)
                if(result.status===0){
                    this.getCategorys()
                }else{
                    message.error('更新分类失败')
                }
            }

    componentWillMount() {
        this.columns = [
             { 
                 title: '分类名称', 
                 dataIndex: 'name', 
               }, 
               { 
                   title: '操作', 
                   width: 300, 
                   render: (category) => (
                        <span> 
                        <LinkButton onClick={() => this.showUpdate(category)}>修改分类 
                        </LinkButton>&nbsp;&nbsp;&nbsp;
                        {this.state.parentId === '0' ? <LinkButton onClick={() => this.showSubCates(category)}>查看子分类</LinkButton> : null} 
                        </span>
   ) 
}];
   }

   componentDidMount() { 
       this.getCategorys() 
    }

    render(){
        const {
                categorys, 
                subCategorys, 
                parentId, 
                parentName, 
                loading, 
                showStatus
                  } = this.state

        const extra = (
             <Button type='primary' onClick={this.showAdd}>
              <Icon type='plus'/> 添加 </Button>
               )

               const category = this.category || {}
               const title = parentId === '0' ? '一级分类列表' : ( 
                   <span> 
                    <LinkButton onClick={this.showCategorys}>一级分类列表</LinkButton> &nbsp;&nbsp; <Icon type='arrow-right' style={{marginRight: 5}}/>&nbsp;&nbsp; 
                      <span>
                        {parentName}
                      </span> 
                   </span> )

        return ( 
    <Card title={title}  extra={extra}>
    <Table 
      bordered 
      rowKey='_id' 
      dataSource={parentId === '0' ? categorys : subCategorys} 
      columns={this.columns} 
      loading={loading} 
      pagination={{pageSize: 5, showQuickJumper: true, showSizeChanger: true}} 
    />
    <Modal
      title="添加分类" 
      visible={showStatus === 1}
      onOk={this.addCategory}
      onCancel={()=>this.setState({showStatus:0})}
    >
    <AddForm
    categorys={categorys}
    parentId={parentId} 
    setForm={(form) => this.form = form}
    />
    </Modal>

    <Modal
    title="修改分类" 
    visible={showStatus === 2} 
    onOk={this.updateCategory} 
    onCancel={() => { 
        this.setState({showStatus: 0}) 
        this.form.resetFields() 
    }}
    >
    <UpdateForm
    categoryName={category.name}
    setForm={(form) => this.form = form}
    />
    </Modal>
    </Card>
             )
    }
}