import React, {Component} from 'react' 
import { Form, Input, Icon, Button} from 'antd' 
import logo from '../../asset/images/logo192.png'
import './login.less'
import {login} from '../../redux/actions'
import {Redirect} from 'react-router-dom'
import {connect} from 'react-redux'

const Item = Form.Item

class Login extends Component {
    login = (e) => { 
        e.preventDefault()
        this.props.form.validateFields(async(err, values) => {
          if (!err) {
              const {username,password}=values
              this.props.login(username, password)
          }else{
              console.log('检验失败!')
          }
        });
      };

render() {
    const user = this.props.user
    if (user && user._id){
      return <Redirect to='/home'/>
    }

    const { getFieldDecorator } = this.props.form;
return ( 
    <div className='login'> 
    <header className='login-header'> 
    <img src={logo} alt="logo"/> 
    <h1>React 项目: 后台管理系统</h1> 
    </header>
<section className='login-content'> 
<h3>用户登陆</h3> 
<Form onSubmit={this.login} className="login-form">
 <Item>
 {getFieldDecorator('username', {
    rules: [
        {required: true, whitespace: true, message: '必须输入用户名'}, 
        {min: 4, message: '用户名必须大于 4 位'}, 
        {max: 12, message: '用户名必须小于 12 位'},
        {pattern:/^[a-zA-Z0-9_]+$/,message: '用户名必须是英文、数组或下划线组成'}
    ],
  })(
    <Input
      prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
      placeholder="用户名"
    />,
  )}
  </Item>
   <Item>
   {getFieldDecorator('password', {
    rules: [
        {required: true, whitespace: true, message: '必须输入密码'}, 
        {min: 4, message: '密码必须大于 4 位'}, 
        {max: 12, message: '密码必须小于 12 位'},
        {pattern:/^[a-zA-Z0-9_]+$/,message: '密码必须是英文、数组或下划线组成'}
    ],
  })(
    <Input
     prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)'}}/>} 
     type="password" placeholder="密码"
     />
  )}
     </Item>
     <Item> 
     <Button type="primary" htmlType="submit" className="login-form-button"> 登录 </Button>
      </Item> 
      </Form> 
      </section> 
      </div>
     )
     }
     }

     const WrapLogin = Form.create()(Login)
     export default connect(
      state => ({user: state.user}),
      {login}
    )(WrapLogin)